import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { MockProvider} from '../../providers/mock/mock';

/**
 * Generated class for the VirinfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-virinfo',
  templateUrl: 'virinfo.html',
})
export class VirinfoPage {

  items: string[];

  constructor(public navCtrl: NavController, public navParams: NavParams, private mockProvider: MockProvider) {
    this.items = mockProvider.getData();
  }

  virtualScroll(){
  this.mockProvider.getAsyncData().then((newData) => {
    for (var i = 0; i < newData.length; i++) {
      this.items.push( newData[i] );
    }
  });
}

  ionViewDidLoad() {
    console.log('ionViewDidLoad VirinfoPage');
  }

}



